/**
 * Returns a normally distributed random number.
 * 
 * The number is unclamped, but will be sampled from a normal distribution
 * defined by the parameters.  As a rule of thumb, about 68% of values will
 * be within onw standard deviation of the mean, and about 95% will be within
 * two.
 * 
 * @param mean The mean value of the distribution.
 * @param stddev The standard deviation of the distribution.
 * @returns A randomly generated float from the distribution.
 */
export function randomNormal(mean: number, stddev: number): number {
  const u = 1 - Math.random(); // Converting [0,1) to (0,1]
  const v = Math.random();
  const z = Math.sqrt(-2.0 * Math.log(u)) * Math.cos(2.0 * Math.PI * v);
  return z * stddev + mean;
}

/**
 * Returns a randomly selected element from `array`.
 * @param array The array to sample from.
 * @returns An element of `array`.
 */
export function selectRandom<T>(array: T[]): T {
  return array[Math.floor(Math.random() * array.length)];
}

/**
 * Returns a randomly selected member of `anEnum`.
 * 
 * @param anEnum Some enum type.
 * @returns A randomly chosen member of `anEnum`.
 */
export function selectRandomEnum<T extends {}>(anEnum: T): T[keyof T] {
  const enumValues = Object.values(anEnum) as unknown as T[keyof T][];
  return selectRandom(enumValues);
}

/**
 * Selects a random choice with some probabilistic weighting.
 * 
 * The keys of `choices` should be the possible outcomes, and the values should
 * be numbers indicating the relative weight of each choice.  The absolute
 * values of the weights do not matter, only the ratio of each to the others.
 * If all weights are the same, every choice will have an equal chance of being
 * selected.
 * 
 * @param choices A map from choices to their relative weights.
 * @returns One of the keys of `choices`.
 */
export function selectWeighted<T>(choices: Map<T, number>): T {
  const total = Array.from(choices.values()).reduce((a, b) => a + b, 0);
  const roll = Math.random() * total;
  let accum = 0.0;
  for (const choice of choices.keys()) {
    accum += choices.get(choice)!;
    if (roll < accum) {
      return choice;
    }
  }
  return Array.from(choices.keys())[0];
}
