import { LitElement, html, css } from "lit";
import { themeCss } from "./theme.js";
import { customElement, property } from "lit/decorators.js";
import type { CharacterModelUpdate, CharacterModel } from "./model.js";
import type { InputTextEvent } from "./types.js";

@customElement("notes-view")
export class NotesView extends LitElement {
  @property({ type: Object })
  model!: CharacterModel;

  static override styles = [
    themeCss,
    css`
      :host {
        display: block;
        padding: 4px 8px;
        border-radius: 2px;
      }

      h2 {
        font-size: 20px;
        text-align: center;
        margin: 0;
        padding: 0;
      }

      section {
        display: flex;
        flex-wrap: wrap;
        background-color: #fff;
        border-radius: 2px;
        border: 1px solid rgba(0, 0, 0, 0.15);
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15),
          inset 0 0 12px var(--paper-yellow-100);
        gap: 4px 12px;
      }

      textarea {
        flex: 1;
        min-height: 100px;
      }
    `,
  ];

  override render() {
    return html`
      <h2>Notes</h2>
      <section>
        <textarea
          .value=${this.model.notes || ""}
          @change=${this.onNotesChanged}
        ></textarea>
      </section>
    `;
  }

  private onNotesChanged(event: InputTextEvent) {
    this.dispatchEvent(
      new CustomEvent<CharacterModelUpdate>("character-change", {
        detail: {
          change: { notes: event.target.value },
        },
        bubbles: true,
        composed: true,
      })
    );
  }
}
