/**
 * Converts a pair of attribute and skill ranks into a die code.
 *
 * In SPIRE, each rank in an attribute or skill changes which dice the player
 * gets to roll when they are making a check against that attribute or skill. A
 * rank of 1 gets you a single d6, the second rank upgrades that to a d8, the
 * third rank upgrades again to a d10, and the fourth rank reverts that first
 * die to a d6 and adds a second d6, which gets upgraded to a d8 at rank five,
 * and so on.  In text, we write this in terms of a number of "base dice" (which
 * are always d6's) and a "bonus die", so:
 *
 * -   Rank 1: 0d6+d6
 * -   Rank 2: 0d6+d8
 * -   Rank 3: 0d6+d10
 * -   Rank 4: 1d6+d6
 * -   Rank 5: 1d6+d8
 *
 * This function computes and returns the die code for a given combination of
 * attribute rank and skill rank.  `attributeRank` must be a positive integer,
 * and `skillRank` must be either zero or a positive integer.  If either of
 * these conditions is violated, the empty string is returned.
 */
export function toDieCode(attributeRank?: number, skillRank?: number, isAttribute: boolean = false): string {
  if (
    attributeRank == null ||
    skillRank == null ||
    attributeRank <= 0 ||
    (!isAttribute && skillRank <= 0)
  ) {
    return "";
  }
  const totalRank = attributeRank + skillRank;
  const bonusDie = totalRank % 3 === 2 ? 10 : totalRank % 3 === 1 ? 8 : 6;
  return `${Math.trunc(totalRank / 3)}d6+d${bonusDie}`;
}
