import { LitElement, html, css } from "lit";
import { toDieCode } from "./die_code.js";
import { themeCss } from "./theme.js";
import { parseIntOrZero, renderIntOrBlank } from "./parsing.js";
import "./skill-view.js";
import { customElement, property } from "lit/decorators.js";
import type {
  CharacterModelUpdate,
  AttributeModel,
  AttributeModelUpdate,
  AddSkillRowEventDetail,
} from "./model.js";
import type { InputTextEvent } from "./types.js";
import { createIcons, Plus } from "lucide";

createIcons({ icons: { Plus } });

@customElement("attribute-view")
export class AttributeView extends LitElement {
  @property({ type: Object })
  model!: AttributeModel;
  @property({ type: Number })
  generalXp: number = 0;
  @property({ type: Boolean })
  showHeader: boolean = false;
  @property({ type: Boolean })
  locked: boolean = true;

  static override styles = [
    themeCss,
    css`
      :host {
        display: flex;
        flex-direction: column;
        flex: 1;
        flex-basis: 0;
        margin-bottom: 8px;
      }

      header {
        display: flex;
        padding-right: 6px;
        padding-top: 4px;
        font-size: 13px;
        font-weight: var(--header-font-weight);
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;
        background-color: var(--header-color);
      }

      section {
        display: flex;
        flex-direction: column;
        flex: 1;
        flex-basis: 0;
        padding: 4px;
        background-color: #fff;
        border-radius: 2px;
        border: 1px solid rgba(0, 0, 0, 0.15);
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15),
          inset 0 0 12px var(--paper-yellow-100);
      }

      #attribute {
        display: flex;
        align-items: baseline;
      }

      header > * {
        text-align: center;
      }

      header > *,
      #attribute > * {
        margin: 0 2px;
      }

      #attribute > :first-child {
        margin-left: 0;
      }

      #attribute > :last-child {
        margin-right: 0;
      }

      .text-column,
      #name {
        font-weight: var(--header-font-weight);
        flex: 1;
        flex-basis: 0;
      }

      .numeric-column,
      input[type="number"] {
        width: 24px;
        flex: none;
      }

      .die-code-column,
      #die-code {
        width: 40px;
        flex: none;
      }

      #die-code {
        font-size: 13px;
      }
    `,
  ];

  override render() {
    return html`
      ${this.showHeader ? this.renderHeader() : html``}
      <section>
        <div id="attribute">
          <button
            id="buy-button"
            class="buy-button"
            title=${this.canAffordRank
              ? `Buy a rank of this attribute for ${this.xpCostToRaise} XP`
              : `Cannot afford a rank of this attribute ` +
                `(need ${this.xpCostToRaise} XP, ` +
                `have ${this.xpAvailable})`}
            ?disabled=${!this.canAffordRank}
            @click=${this.onBuyRankButtonClicked}
          >
            <lucide-icon .icon=${Plus} .size=${16}></lucide-icon>
          </button>
          <div id="name">${this.model.name}</div>
          <input
            type="number"
            id="rank"
            min="1"
            ?readonly=${this.locked}
            .value=${renderIntOrBlank(this.model.rank)}
            @change=${this.onRankChanged}
          />
          <div id="die-code">${toDieCode(this.model.rank, 0, true)}</div>
          <input
            type="number"
            id="xp"
            min="0"
            .value=${renderIntOrBlank(this.model.xp)}
            @change=${this.onXpFieldChanged}
          />
        </div>
        ${this.model.skills.map(
          (model) =>
            html`<skill-view
              .model=${model}
              .attributeModel=${this.model}
              .generalXp=${this.generalXp}
              .locked=${this.locked}
            ></skill-view>`
        )}
        ${!this.locked ? this.renderEditUi() : html``}
      </section>
    `;
  }

  private renderHeader() {
    return html`
      <header>
        <div class="text-column"></div>
        <div class="numeric-column">Rank</div>
        <div class="die-code-column">Dice</div>
        <div class="numeric-column">XP</div>
      </header>
    `;
  }

  private renderEditUi() {
    return html`<button @click=${this.onAddButtonClicked}>Add skill</button>`;
  }

  get xpAvailable() {
    return (this.generalXp ?? 0) + (this.model.xp ?? 0);
  }

  get xpCostToRaise() {
    return ((this.model.rank ?? 0) + 1) * 5;
  }

  get canAffordRank() {
    return this.xpAvailable >= this.xpCostToRaise;
  }

  onRankChanged(event: InputTextEvent) {
    this.dispatchEvent(
      new CustomEvent<AttributeModelUpdate>("attribute-change", {
        detail: {
          id: this.model.id,
          change: {
            rank: parseIntOrZero(event.target.value),
          },
        },
        bubbles: true,
        composed: true,
        cancelable: false,
      })
    );
  }

  onXpFieldChanged(event: InputTextEvent) {
    this.dispatchEvent(
      new CustomEvent<AttributeModelUpdate>("attribute-change", {
        detail: {
          id: this.model.id,
          change: { xp: parseIntOrZero(event.target.value) },
        },
        bubbles: true,
        composed: true,
        cancelable: false,
      })
    );
  }

  onBuyRankButtonClicked(event: Event) {
    const xpCost = ((this.model.rank ?? 0) + 1) * 5;
    const generalXpSpent = Math.max(0, xpCost - (this.model.xp ?? 0));
    this.dispatchEvent(
      new CustomEvent<AttributeModelUpdate>("attribute-change", {
        detail: {
          id: this.model.id,
          change: {
            rank: (this.model.rank ?? 0) + 1,
            xp: Math.max(0, (this.model.xp ?? 0) - xpCost),
          },
        },
        bubbles: true,
        composed: true,
        cancelable: false,
      })
    );
    if (generalXpSpent > 0) {
      this.dispatchEvent(
        new CustomEvent<CharacterModelUpdate>("character-change", {
          detail: {
            change: { xp: this.generalXp - generalXpSpent },
          },
          bubbles: true,
          composed: true,
          cancelable: false,
        })
      );
    }
  }

  onAddButtonClicked(event: Event) {
    this.dispatchEvent(
      new CustomEvent<AddSkillRowEventDetail>("add-skill-row", {
        detail: {
          id: this.model.id,
        },
        bubbles: true,
        composed: true,
        cancelable: false,
      })
    );
  }
}
