import { LitElement, html, css, type PropertyValues } from "lit";
import { themeCss } from "./theme.js";
import { customElement, property, query, state } from "lit/decorators.js";
import type { CharacterModelUpdate, CharacterModel } from "./model.js";
import type { InputTextEvent } from "./types.js";
import { when } from "lit/directives/when.js";
import { BookImage, createIcons, Ungroup } from "lucide";
import "./icon-button.js";
import "./lucide-icon.js";
import "./loading-spinner";

createIcons({
  icons: [BookImage],
});

@customElement("portrait-view")
export class PortraitView extends LitElement {
  @property({ type: Object })
  model!: CharacterModel;

  @query("#file-picker")
  filePicker?: HTMLInputElement;

  @state()
  private loading: boolean = false;

  static override styles = [
    themeCss,
    css`
      :host {
        display: flex;
        flex-direction: column;
        padding: 4px 8px;
        border-radius: 2px;
        position: relative;
      }

      h2 {
        font-size: 20px;
        text-align: center;
        margin: 0;
        padding: 0;
      }

      section {
        height: 100%;
        flex: 1;
        display: flex;
        align-items: stretch;
        justify-content: stretch;
        background-color: #fff;
        border-radius: 2px;
        border: 1px solid rgba(0, 0, 0, 0.15);
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15),
          inset 0 0 12px var(--paper-yellow-100);
        gap: 4px 12px;
      }

      #placeholder {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;
      }

      #image {
        flex: 1;
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      #image icon-button {
        display: none;
      }
      #image:hover icon-button {
        display: block;
        opacity: 0.5;
        background-color: var(--surface-color);
      }

      #image icon-button:hover {
        opacity: 1;
      }
    `,
  ];

  override render() {
    return html`
      <h2>Portrait</h2>
      <input
        id="file-picker"
        type="file"
        hidden
        @change=${this.onPortraitPicked}
      />
      <section>
        ${when(
          this.model.portraitUrl,
          () =>
            html`<div
              id="image"
              style="background-image: url('${this.model
                .portraitUrl}'); background-color: ${this.model.portraitBg}"
              @click=${this.choosePortrait}
            >
              <icon-button
                title="Replace portrait"
                @click=${this.choosePortrait}
              >
                <lucide-icon size="32" .icon=${BookImage}></lucide-icon>
              </icon-button>
            </div>`,
          () => html`<div id="placeholder" @click=${this.choosePortrait}>
            ${when(
              this.loading,
              () => html`<loading-spinner></loading-spinner>`,
              () => html`<icon-button
                title="Add portrait"
                @click=${this.choosePortrait}
              >
                <lucide-icon size="32" .icon=${BookImage}></lucide-icon>
              </icon-button>`
            )}
          </div>`
        )}
      </section>
    `;
  }

  private choosePortrait(e: Event) {
    this.filePicker?.click();
    e.stopPropagation();
  }

  private onPortraitPicked(e: Event) {
    const file = this.filePicker?.files?.[0];
    if (!file) {
      return;
    }

    this.loading = true;
    const reader = new FileReader();
    reader.onload = (e) => {
      const fullSizeDataUri = e.target!.result as string;
      const img = new Image();
      img.src = fullSizeDataUri;
      img.onload = (e) => {
        const canvas = document.createElement("canvas");
        const scaleFactor = Math.min(1.0, 650.0 / img.width);
        canvas.width = img.width * scaleFactor;
        canvas.height = img.height * scaleFactor;

        const context = canvas.getContext("2d", { willReadFrequently: true });
        context?.scale(scaleFactor, scaleFactor);
        context?.drawImage(img, 0, 0);
        const topLeftColor = context?.getImageData(0, 0, 1, 1).data;
        const portraitBg = topLeftColor
          ? `#${(
              (topLeftColor[0] << 16) |
              (topLeftColor[1] << 8) |
              topLeftColor[2]
            ).toString(16)}`
          : "";

        this.dispatchEvent(
          new CustomEvent<CharacterModelUpdate>("character-change", {
            detail: {
              change: {
                portraitUrl: canvas.toDataURL("image/jpeg", 0.7),
                portraitBg,
              },
            },
            bubbles: true,
            composed: true,
          })
        );
        this.loading = false;
      };
    };
    reader.readAsDataURL(file);
  }
}
