import { css, html, LitElement } from "lit";
import { customElement, property, query } from "lit/decorators.js";
import {
  type AttributeModel,
  type CharacterModel,
  type CharacterModelUpdate,
} from "./model.js";
import { themeCss } from "./theme.js";
import { parseIntOrZero } from "./parsing.js";
import {
  randomNormal,
  selectRandom,
  selectRandomEnum,
  selectWeighted,
} from "./random.js";

enum NPCDomainFocus {
  PHYSICAL = 0,
  SOCIAL,
  MENTAL,
}

enum NPCAspectFocus {
  POWER = 0,
  FINESSE,
  SENSE,
}

interface NPCParameters {
  name?: string;
  archetype?: string;
  age?: string;
  gender?: string;
  presentation?: string;
  primeValue?: string;
  primeFear?: string;
  focusedDomain: NPCDomainFocus;
  focusedAspect: NPCAspectFocus;
  minMaxFactor: number;
  averageAttributeRank: number;
  hasTraits: boolean;
}

@customElement("npc-generator")
export class NPCGenerator extends LitElement {
  @property({ type: Object })
  model!: CharacterModel;

  @query("dialog")
  _dialog?: HTMLDialogElement;

  static override styles = [
    themeCss,
    css`
      h2 {
        margin: 0 0 16px;
      }
      div {
        font-family: var(--body-font-family);
      }
      .content {
        display: flex;
        flex-direction: column;
        gap: 8px;
      }
      .row {
        display: flex;
        gap: 16px;
      }
      .row > span {
        flex: 1;
      }
      .row > :last-child {
        width: 180px;
      }
      .slider {
        display: flex;
        gap: 4px;
      }
      .buttons {
        margin-top: 16px;
        display: flex;
        justify-content: flex-end;
        gap: 8px;
      }
      dialog {
        border: none;
        box-shadow: inset 0 0 49px #e0dcbf, 0 6px 8px rgba(0, 0, 0, 0.25);
        padding: 16px;
      }
    `,
  ];

  override render() {
    return html`<dialog @close=${this._generateNPC}>
      <h2>NPC Configuration</h2>
      <div class="content">
        <div class="row">
          <span>Average attribute rank:</span>
          <input id="rank" type="number" max="99" value="5" />
        </div>
        <div class="row">
          <span>Focused domain:</span>
          <select id="focused-domain">
            <option value=${NPCDomainFocus.PHYSICAL}>Physical</option>
            <option value=${NPCDomainFocus.SOCIAL}>Social</option>
            <option value=${NPCDomainFocus.MENTAL}>Mental</option>
          </select>
        </div>
        <div class="row">
          <span>Focused aspect:</span>
          <select id="focused-aspect">
            <option value=${NPCAspectFocus.POWER}>Power</option>
            <option value=${NPCAspectFocus.FINESSE}>Finesse</option>
            <option value=${NPCAspectFocus.SENSE}>Sense</option>
          </select>
        </div>
        <div class="row">
          <span>MinMax factor:</span>
          <div class="slider">
            <span>0</span>
            <input
              id="min-max-factor"
              type="range"
              min="0.0"
              max="1.0"
              value="0.5"
              step="0.05"
            />
            <span>Péťa</span>
          </div>
        </div>
      </div>
      <div class="buttons">
        <button @click=${() => this._dialog?.close("cancel")}>Cancel</button>
        <button @click=${() => this._dialog?.close("confirm")}>Generate</button>
      </div>
    </dialog>`;
  }

  configure(): void {
    this._dialog!.showModal();
  }

  private _generateRandomName() {
    return "Pirate Steve";
  }

  private _generateRandomAge() {
    return Math.round(randomNormal(35, 10)).toString();
  }

  private _pickRandomArchetype() {
    return selectRandom([
      "Swashbuckler",
      "Scoundrel",
      "Navigator",
      "Dispossessed",
      "Chronicler",
      "Bruiser",
      "Aristocrat",
      "Alchemist",
      "Administrator",
    ]);
  }

  private _pickRandomGender() {
    return selectWeighted(
      new Map(
        Object.entries({
          female: 1.0,
          male: 1.0,
          AFAB: 0.2,
          AMAB: 0.2,
          intersex: 0.1,
        })
      )
    );
  }

  private _pickRandomPresentation() {
    return "";
  }
  private _pickRandomPrimeValue() {
    return "";
  }
  private _pickRandomPrimeFear() {
    return "";
  }
  private _pickRandomTraits() {
    return "";
  }
  private _generateAttributes(
    params: {
      averageRank: number;
      minMaxFactor: number;
      focusedDomain: NPCDomainFocus;
      focusedAspect: NPCAspectFocus;
    },
    model: AttributeModel[]
  ): AttributeModel[] {
    // Roll up a character with an appropriate number of attribute points.
    // Use minMaxFactor to determine how evenly to allocate points, and
    // focus to determine which attributes to favor.
    //
    // For each attribute, roll a number normally distributed around 1 with some stddev.
    // Normalize these scores so that their sum is equal to the number of available attribute points.
    // Then, for some number of iterations based on minMaxFactor, select a focus attribute at random
    // and remove a point from a non-focused attribute whose rank is above 1 to add it to the chosen favored attribute instead.
    enum Attribute {
      STRENGTH = 0,
      DEXTERITY = 1,
      PERCEPTION = 2,
      PRESENCE = 3,
      GRACE = 4,
      INTUITION = 5,
      WILL = 6,
      WITS = 7,
      AWARENESS = 8,
    }
    const availablePoints = params.averageRank * 9;
    const rawAttrs = Array(9)
      .fill(0)
      .map(() => randomNormal(1.0, 0.2));
    const sum = rawAttrs.reduce((a, b) => a + b, 0);
    const normalizedAttrs = rawAttrs.map((a) =>
      Math.round((a / sum) * availablePoints)
    );
    const isFocusedDomain = (attr: Attribute) =>
      attr >= params.focusedDomain * 3 && attr < (params.focusedDomain + 1) * 3;
    const isFocusedAspect = (attr: Attribute) =>
      attr % 3 == params.focusedAspect;
    for (
      let i = 0;
      i < Math.round((availablePoints / 4) * params.minMaxFactor);
      ++i
    ) {
      let destination: Attribute;
      do {
        destination = selectRandomEnum(Attribute);
      } while (!isFocusedDomain(destination));
      let source: Attribute;
      do {
        source = selectRandomEnum(Attribute);
      } while (isFocusedDomain(source) || normalizedAttrs[source] <= 1);
      --normalizedAttrs[source];
      ++normalizedAttrs[destination];
      do {
        destination = selectRandomEnum(Attribute);
      } while (!isFocusedAspect(destination));
      do {
        source = selectRandomEnum(Attribute);
      } while (isFocusedAspect(source) || normalizedAttrs[source] <= 1);
      --normalizedAttrs[source];
      ++normalizedAttrs[destination];
    }

    return model.map((m, i) => ({ ...m, rank: normalizedAttrs[i] }));
  }

  private _generateNPC() {
    if (this._dialog?.returnValue != "confirm") {
      return;
    }
    const params: NPCParameters = {
      averageAttributeRank: parseIntOrZero(
        (this.shadowRoot?.querySelector("#rank") as HTMLInputElement).value
      ),
      focusedDomain: parseIntOrZero(
        (this.shadowRoot?.querySelector("#focused-domain") as HTMLSelectElement)
          .value
      ),
      focusedAspect: parseIntOrZero(
        (this.shadowRoot?.querySelector("#focused-aspect") as HTMLSelectElement)
          .value
      ),
      minMaxFactor: parseFloat(
        (this.shadowRoot?.querySelector("#min-max-factor") as HTMLInputElement)
          .value
      ),
      hasTraits: false,
    };

    this.dispatchEvent(
      new CustomEvent<CharacterModelUpdate>("character-change", {
        detail: {
          change: {
            name: this._generateRandomName(),
            age: this._generateRandomAge(),
            player: "NPC",
            archetype: this._pickRandomArchetype(),
            gender: this._pickRandomGender(),
            presentation: this._pickRandomPresentation(),
            primeValue: this._pickRandomPrimeValue(),
            primeFear: this._pickRandomPrimeFear(),
            traits: params.hasTraits ? this._pickRandomTraits() : "",
            attributes: this._generateAttributes(
              {
                averageRank: params.averageAttributeRank,
                minMaxFactor: params.minMaxFactor,
                focusedDomain: params.focusedDomain,
                focusedAspect: params.focusedAspect,
              },
              this.model.attributes
            ),
          },
        },
        bubbles: true,
        composed: true,
        cancelable: false,
      })
    );
  }
}
