import { LitElement, html, css } from "lit";
import { themeCss } from "./theme.js";
import { parseIntOrZero, renderIntOrBlank } from "./parsing.js";
import { customElement, property } from "lit/decorators.js";
import type { CharacterModelUpdate, CharacterModel } from "./model.js";
import type { InputTextEvent } from "./types.js";

@customElement("general-xp-view")
export class GeneralXPView extends LitElement {
  @property({ type: Object })
  model!: CharacterModel;

  static override styles = [
    themeCss,
    css`
      :host {
        display: flex;
        padding: 4px;
        margin: 8px;
        background-color: #fff;
        border-radius: 2px;
        border: 1px solid rgba(0, 0, 0, 0.15);
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15),
          inset 0 0 12px var(--paper-yellow-100);
        gap: 4px 12px;
        page-break-inside: avoid;
        break-inside: avoid;
        -webkit-region-break-inside: avoid;
      }

      #label {
        font-weight: var(--header-font-weight);
        text-align: center;
        white-space: nowrap;
      }

      input {
        flex: 1;
      }
    `,
  ];

  override render() {
    return html`
      <div id="label">General XP</div>
      <input
        type="number"
        .value=${renderIntOrBlank(this.model.xp)}
        @change=${this.onXPChanged}
      />
    `;
  }

  private onXPChanged(event: InputTextEvent) {
    let newXp = parseIntOrZero(event.target.value);
    if (isNaN(newXp)) {
      newXp = 0;
    }
    this.dispatchEvent(
      new CustomEvent<CharacterModelUpdate>("character-change", {
        detail: {
          change: { xp: newXp },
        },
        bubbles: true,
        composed: true,
      })
    );
  }
}
