import { LitElement, html, css, type TemplateResult } from "lit";
import { themeCss } from "./theme.js";
import "./attribute-view.js";
import { customElement, property } from "lit/decorators.js";
import type { CharacterModel } from "./model.js";

@customElement("attributes-view")
export class AttributesView extends LitElement {
  @property({ type: Object })
  model!: CharacterModel;
  @property({ type: Number })
  columnCount: number = 3;
  @property({ type: Boolean })
  locked: boolean = true;

  static override styles = [
    themeCss,
    css`
      :host {
        position: relative;
        display: block;
        padding: 4px 8px;
        border-radius: 2px;
        page-break-inside: avoid;
        break-inside: avoid;
        -webkit-region-break-inside: avoid;
      }

      .attribute-group {
        display: flex;
        flex: 1 1 0;
      }

      .attribute-group > :not(:last-child) {
        margin-right: 8px;
      }
    `,
  ];

  override connectedCallback() {
    super.connectedCallback();
    this.recalculateColumns();
    window.addEventListener("resize", (e) => this.recalculateColumns());
  }

  override render() {
    return html`
      <h2>Attributes</h2>
      ${this.renderAttributes()}
    `;
  }

  private renderAttributes() {
    const rows: TemplateResult[][] = [];
    this.model.attributes.forEach((attribute, index) => {
      const domResult = html`<attribute-view
        .model=${attribute}
        .generalXp=${this.model.xp}
        .showHeader=${index < this.columnCount}
        .locked=${this.locked}
      ></attribute-view>`;
      if (index % this.columnCount === 0) {
        rows.push([domResult]);
      } else {
        rows[rows.length - 1].push(domResult);
      }
    });
    return rows.map((row) => html`<div class="attribute-group">${row}</div>`);
  }

  private recalculateColumns() {
    if (window.innerWidth <= 500) {
      this.columnCount = 1;
    } else if (window.innerWidth <= 650) {
      this.columnCount = 2;
    } else {
      this.columnCount = 3;
    }
  }
}
