import { LitElement, html, css } from "lit";
import { themeCss } from "./theme.js";
import "./specialization-view.js";
import { customElement, property } from "lit/decorators.js";
import type { AddSpecializationRowEventDetail, CharacterModel } from "./model.js";

@customElement("specializations-view")
export class SpecializationsView extends LitElement {
  @property({ type: Object })
  model!: CharacterModel;
  @property({ type: Number })
  columnCount: number = 3;
  @property({ type: Boolean })
  locked: boolean = true;

  static override styles = [
    themeCss,
    css`
      :host {
        position: relative;
        display: flex;
        flex-direction: column;
        flex: 1;
        flex-basis: 0;
        padding: 4px 8px;
        page-break-inside: avoid;
        break-inside: avoid;
        -webkit-region-break-inside: avoid;
      }

      header {
        display: flex;
        padding: 4px;
        padding-bottom: 0;
        font-size: 13px;
        font-weight: var(--header-font-weight);
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;
        background-color: var(--header-color);
        gap: 4px 12px;
      }

      section {
        display: flex;
        flex-wrap: wrap;
        flex: 1;
        flex-basis: 0;
        justify-content: space-between;
        padding: 4px;
        background-color: #fff;
        border-radius: 2px;
        border: 1px solid rgba(0, 0, 0, 0.15);
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15),
          inset 0 0 12px var(--paper-yellow-100);
        gap: 0 12px;
      }

      specialization-view {
        width: calc(33.33% - 12px);
      }

      header > * {
        margin: 0 2px;
      }

      .header-column {
        width: calc(33.33% - 12px);
        display: flex;
        gap: 0 4px;
        justify-content: space-between;
      }

      .text-column {
        font-weight: var(--header-font-weight);
        flex: 1;
        flex-basis: 0;
      }

      .numeric-column {
        width: 28px;
        flex: none;
        text-align: center;
      }

      .die-code-column {
        width: 40px;
        flex: none;
        text-align: center;
      }

      #edit-ui {
        display: flex;
        justify-content: space-around;
        width: 100%;
      }

      #edit-ui > button {
        flex: 1;
        max-width: 33.33%;
      }

      @media screen and (max-width: 650px) {
        specialization-view {
          width: calc(50% - 12px);
        }

        .header-column {
          width: calc(50% - 12px);
        }

        #edit-ui > button {
          max-width: 50%;
        }
      }

      @media screen and (max-width: 500px) {
        specialization-view {
          width: calc(100% - 12px);
        }

        .header-column {
          width: calc(100% - 12px);
        }

        #edit-ui > button {
          max-width: 100%;
        }
      }
    `,
  ];

  override connectedCallback() {
    super.connectedCallback();
    this.recalculateColumns();
    window.addEventListener("resize", (e) => this.recalculateColumns());
  }

  override render() {
    return html`
      <h2>Specializations</h2>
      <header>
        ${Array.from(Array(this.columnCount).keys()).map(
          () => html`<div class="header-column">
            <div class="text-column"></div>
            <div class="numeric-column">Rank</div>
            <div class="die-code-column">Bonus</div>
            <div class="numeric-column last-column">XP</div>
          </div>`
        )}
      </header>
      <section>
        ${this.model.specializations.map(
          (model) =>
            html`<specialization-view
              .model=${model}
              .generalXp=${this.model.xp}
              .locked=${this.locked}
            ></specialization-view>`
        )}
        ${!this.locked ? this.renderEditUi() : html``}
      </section>
    `;
  }

  private renderEditUi() {
    return html`<div id="edit-ui">
      <button @click=${this.onAddButtonClicked}>Add row</button>
    </div>`;
  }

  private recalculateColumns() {
    if (window.innerWidth <= 500) {
      this.columnCount = 1;
    } else if (window.innerWidth <= 650) {
      this.columnCount = 2;
    } else {
      this.columnCount = 3;
    }
  }

  private onAddButtonClicked(event: Event) {
    this.dispatchEvent(
      new CustomEvent<AddSpecializationRowEventDetail>("add-specialization-row", {
        detail: {},
        bubbles: true,
        composed: true,
      })
    );
  }
}
