import { LitElement, html, css } from "lit";
import { themeCss } from "./theme.js";
import { customElement, property } from "lit/decorators.js";
import type { EquipmentModelUpdate, EquipmentModel } from "./model.js";
import type { InputTextEvent } from "./types.js";

@customElement("equipment-view")
export class EquipmentView extends LitElement {
  @property({ type: Object })
  model!: EquipmentModel;
  @property({ type: Boolean })
  showHeader: boolean = false;
  @property({ type: Boolean })
  locked: boolean = true;

  static override styles = [
    themeCss,
    css`
      :host {
        display: block;
      }

      header {
        display: flex;
        padding: 2px 4px;
        font-size: 13px;
        font-weight: var(--header-font-weight);
        border-radius: 4px;
        background-color: var(--header-color);
      }

      header > *,
      #content > * {
        margin-right: 12px;
      }

      header > :last-of-type,
      #content > :last-of-type {
        margin-right: 0;
      }

      #content {
        display: flex;
      }

      #name-header,
      #equipment-name {
        flex: 3 0 0;
      }
      #bonus-header,
      #bonus {
        flex: 1;
        min-width: 0;
      }
    `,
  ];

  override render() {
    return html`
      ${this.showHeader ? this.renderHeader() : html``}
      <div id="content">
        <input
          type="text"
          id="equipment-name"
          .value=${this.model.name}
          @change=${this.onNameChanged}
        />
        <input
          type="text"
          id="bonus"
          .value=${this.model.bonus}
          @change=${this.onBonusChanged}
        />
      </div>
    `;
  }

  private renderHeader() {
    return html`
      <header>
        <span id="name-header">Equipment</span>
        <span id="bonus-header">Bonus</span>
      </header>
    `;
  }

  private onNameChanged(event: InputTextEvent) {
    this.dispatchEvent(
      new CustomEvent<EquipmentModelUpdate>("equipment-change", {
        detail: {
          id: this.model.id,
          change: {
            name: event.target.value,
          },
        },
        bubbles: true,
        composed: true,
      })
    );
  }

  private onBonusChanged(event: InputTextEvent) {
    this.dispatchEvent(
      new CustomEvent<EquipmentModelUpdate>("equipment-change", {
        detail: {
          id: this.model.id,
          change: {
            bonus: event.target.value,
          },
        },
        bubbles: true,
        composed: true,
      })
    );
  }
}
