import { LitElement, html, css } from "lit";
import { themeCss } from "./theme.js";
import { customElement, property } from "lit/decorators.js";
import type { ItemModel, ItemModelUpdate } from "./model.js";
import { renderIntOrBlank } from "./parsing.js";
import type { InputTextEvent } from "./types.js";

@customElement("item-view")
export class ItemView extends LitElement {
  @property({ type: Object })
  model!: ItemModel;
  @property({ type: Boolean })
  showHeader: boolean = false;
  @property({ type: Boolean })
  locked: boolean = true;

  static override styles = [
    themeCss,
    css`
      :host {
        display: block;
      }

      header {
        display: flex;
        padding: 2px 4px;
        font-size: 13px;
        font-weight: var(--header-font-weight);
        border-radius: 4px;
        background-color: var(--header-color);
      }

      header > *,
      #content > * {
        margin-right: 12px;
      }

      header > :last-of-type,
      #content > :last-of-type {
        margin-right: 0;
      }

      #content {
        display: flex;
      }

      #name-header,
      #item-name {
        flex: 3 0 0;
      }
      #quantity-header,
      #quantity {
        flex: 1;
        min-width: 0;
      }
    `,
  ];

  override render() {
    return html`
      ${this.showHeader ? this.renderHeader() : html``}
      <div id="content">
        <input
          type="text"
          id="item-name"
          .value=${this.model.name || ""}
          @change=${this.onNameChanged}
        />
        <input
          type="number"
          id="quantity"
          .value=${renderIntOrBlank(this.model.quantity)}
          @change=${this.onQuantityChanged}
        />
      </div>
    `;
  }

  private renderHeader() {
    return html`
      <header>
        <span id="name-header">Items</span>
        <span id="quantity-header">Quantity</span>
      </header>
    `;
  }

  private onNameChanged(event: InputTextEvent) {
    this.dispatchEvent(
      new CustomEvent<ItemModelUpdate>("item-change", {
        detail: {
          id: this.model.id,
          change: {
            name: event.target.value,
          },
        },
        bubbles: true,
        composed: true,
      })
    );
  }

  private onQuantityChanged(event: InputTextEvent) {
    this.dispatchEvent(
      new CustomEvent<ItemModelUpdate>("item-change", {
        detail: {
          id: this.model.id,
          change: {
            quantity: Number.parseFloat(event.target.value),
          },
        },
        bubbles: true,
        composed: true,
      })
    );
  }
}
