import { LitElement, html, css } from "lit";
import { themeCss } from "./theme.js";
import { customElement, property } from "lit/decorators.js";
import type { FatigueLevelModel, FatigueLevelModelUpdate } from "./model.js";
import type { InputTextEvent } from "./types.js";

@customElement("fatigue-level-view")
export class FatigueLevelView extends LitElement {
  @property({ type: Object })
  model!: FatigueLevelModel;
  @property({ type: Boolean })
  showHeader: boolean = false;

  static override styles = [
    themeCss,
    css`
      :host {
        display: block;
      }

      header {
        display: flex;
        padding: 2px 0;
        font-size: 13px;
        font-weight: var(--header-font-weight);
        border-radius: 4px;
        background-color: var(--header-color);
      }

      #checkbox-header,
      input[type="checkbox"] {
        width: 18px;
      }

      header > *,
      #content > * {
        margin-right: 12px;
      }

      #content {
        display: flex;
      }

      #level-header,
      #level {
        flex: 1;
      }
      #effects-header,
      #effects {
        flex: 1 0 0;
      }
    `,
  ];

  override render() {
    return html`
      ${this.showHeader ? this.renderHeader() : html``}
      <div id="content">
        <input
          type="checkbox"
          .checked=${this.model.checked}
          @change=${this.onCheckedChanged}
        />
        <span id="level">${this.model.level}</span>
        <span id="effects">${this.model.effects}</span>
      </div>
    `;
  }

  private renderHeader() {
    return html`
      <header>
        <span id="checkbox-header"></span>
        <span id="level-header">Fatigue</span>
        <span id="effects-header">Effects</span>
      </header>
    `;
  }

  private onCheckedChanged(event: InputTextEvent) {
    this.dispatchEvent(
      new CustomEvent<FatigueLevelModelUpdate>("fatigue-level-change", {
        detail: {
          id: this.model.id,
          checked: event.target.checked,
        },
        bubbles: true,
        composed: true,
      })
    );
  }
}
