import { LitElement, html, css } from "lit";
import { customElement, property } from "lit/decorators.js";
import { classMap } from 'lit/directives/class-map.js';
import { toDieCode } from "./die_code";
import { themeCss } from "./theme";
import { parseIntOrZero, renderIntOrBlank } from "./parsing";
import type {
  SkillModelUpdate,
  AttributeModel,
  SkillModel,
  CharacterModelUpdate,
} from "./model";
import type { InputTextEvent } from "./types";
import { createIcons, Plus } from "lucide";
import "./lucide-icon";

createIcons({ icons: { Plus } });

@customElement("skill-view")
export class SkillView extends LitElement {
  @property({ type: Object })
  model!: SkillModel;
  @property({ type: Object })
  attributeModel!: AttributeModel;
  @property({ type: Number })
  generalXp: number = 0;
  @property({ type: Boolean })
  locked: boolean = true;

  static override styles = [
    themeCss,
    css`
      :host {
        display: flex;
        align-items: baseline;
      }

      :host > * {
        margin: 0 2px;
      }

      :host > :first-child {
        margin-left: 0;
      }

      :host > :last-child {
        margin-right: 0;
      }

      #skill-name {
        flex: 1;
        flex-basis: 0;
        width: 0;
      }

      input[type="number"] {
        width: 24px;
        flex: none;
      }

      #die-code {
        font-size: 13px;
        width: 40px;
        flex: none;
      }

      #xp {
        margin-right: 0;
      }

      .ghosted {
        opacity: 0.5;
      }
    `,
  ];

  override render() {
    return html`
      <button
        id="buy-button"
        class="buy-button"
        ?disabled=${!this.canAffordRank || !this.model.name}
        title=${this.canAffordRank
          ? `Buy a rank of this skill for ${this.xpCostToRaise} XP`
          : `Cannot afford a rank of this skill (need ${this.xpCostToRaise} XP, ` +
            `have ${this.xpAvailable})`}
        @click=${this.onBuyRankButtonClicked}
      >
        <lucide-icon .icon=${Plus} .size=${16}></lucide-icon>
      </button>
      <input
        type="text"
        id="skill-name"
        class="${classMap({ ghosted: (this.model.rank ?? 0) == 0 })}"
        .value=${this.model.name}
        @change=${this.onNameFieldChanged}
      />
      <input
        type="number"
        id="rank"
        min="0"
        ?readonly=${this.locked}
        .value=${renderIntOrBlank(this.model.rank)}
        @change=${this.onRankFieldChanged}
      />
      <div id="die-code">
        ${toDieCode(this.attributeModel.rank, this.model.rank)}
      </div>
      <input
        type="number"
        id="xp"
        min="0"
        .value=${renderIntOrBlank(this.model.xp)}
        @change=${this.onXpFieldChanged}
      />
    `;
  }

  override connectedCallback() {
    super.connectedCallback();
  }

  get xpAvailable() {
    return (this.generalXp ?? 0) + (this.model.xp ?? 0);
  }

  get xpCostToRaise() {
    return (this.model.rank ?? 0) + 1;
  }

  get canAffordRank() {
    return this.xpAvailable >= this.xpCostToRaise;
  }

  private onNameFieldChanged(event: InputTextEvent) {
    this.dispatchEvent(
      new CustomEvent<SkillModelUpdate>("skill-change", {
        detail: {
          attribute: this.attributeModel.id,
          id: this.model.id,
          change: { name: event.target.value },
        },
        bubbles: true,
        composed: true,
        cancelable: false,
      })
    );
  }

  private onRankFieldChanged(event: InputTextEvent) {
    this.dispatchEvent(
      new CustomEvent<SkillModelUpdate>("skill-change", {
        detail: {
          attribute: this.attributeModel.id,
          id: this.model.id,
          change: {
            rank: parseIntOrZero(event.target.value),
          },
        },
        bubbles: true,
        composed: true,
        cancelable: false,
      })
    );
  }

  private onXpFieldChanged(event: InputTextEvent) {
    this.dispatchEvent(
      new CustomEvent<SkillModelUpdate>("skill-change", {
        detail: {
          attribute: this.attributeModel.id,
          id: this.model.id,
          change: {
            xp: parseIntOrZero(event.target.value),
          },
        },
        bubbles: true,
        composed: true,
        cancelable: false,
      })
    );
  }

  private onBuyRankButtonClicked(event: Event) {
    const generalXpSpent = Math.max(
      0,
      this.xpCostToRaise - (this.model.xp ?? 0)
    );
    this.dispatchEvent(
      new CustomEvent<SkillModelUpdate>("skill-change", {
        detail: {
          attribute: this.attributeModel.id,
          id: this.model.id,
          change: {
            rank: (this.model.rank ?? 0) + 1,
            xp: Math.max(0, (this.model.xp ?? 0) - this.xpCostToRaise),
          },
        },
        bubbles: true,
        composed: true,
        cancelable: false,
      })
    );
    if (generalXpSpent > 0) {
      this.dispatchEvent(
        new CustomEvent<CharacterModelUpdate>("character-change", {
          detail: {
            change: { xp: this.generalXp - generalXpSpent },
          },
          bubbles: true,
          composed: true,
          cancelable: false,
        })
      );
    }
  }
}
