import { LitElement, html, css } from "lit";
import { themeCss } from "./theme.js";
import "./wound-level-view.js";
import "./fatigue-level-view.js";
import { customElement, property } from "lit/decorators.js";
import type { CharacterModel, WealthLevelModelUpdate } from "./model.js";
import type { InputTextEvent } from "./types.js";

@customElement("wealth-view")
export class WealthView extends LitElement {
  @property({ type: Object })
  model!: CharacterModel;

  static override styles = [
    themeCss,
    css`
      :host {
        position: relative;
        display: block;
        padding: 4px 8px;
        border-radius: 2px;
        page-break-inside: avoid;
        break-inside: avoid;
        -webkit-region-break-inside: avoid;
      }

      h2 {
        font-size: 20px;
        text-align: center;
        margin: 0;
        padding: 0;
      }

      section {
        display: flex;
        flex-direction: column;
        padding: 4px;
        background-color: #fff;
        border-radius: 2px;
        border: 1px solid rgba(0, 0, 0, 0.15);
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15),
          inset 0 0 12px var(--paper-yellow-100);
        gap: 4px 12px;
      }

      table {
        border-spacing: 12px 0;
        margin: 0 -12px;
      }

      th {
        padding: 2px 0;
        font-size: 13px;
        font-weight: var(--header-font-weight);
        border-radius: 4px;
        background-color: var(--header-color);
        margin: 12px;
      }

      td:first-of-type {
        font-weight: var(--header-font-weight);
        width: auto;
        text-align: right;
      }

      td:not(:first-of-type) {
        width: 50%;
      }

      th:empty {
        background-color: transparent;
      }

      th,
      td {
        text-align: center;
      }
    `,
  ];

  override render() {
    return html`
      <h2>Wealth</h2>

      <section>
        <table>
          <tr>
            <th></th>
            <th>Party Wealth</th>
            <th>Individual Wealth</th>
          </tr>
          ${this.model.wealth.map(
            (wealthLevel) => html`
              <tr>
                <td>${wealthLevel.name}</td>
                <td>
                  <input
                    type="radio"
                    .checked=${wealthLevel.selectedForParty}
                    @change=${this.partyWealthLevelChangedHandler(
                      wealthLevel.id
                    )}
                  />
                </td>
                <td>
                  <input
                    type="radio"
                    .checked=${wealthLevel.selectedForIndividual}
                    @change=${this.individualWealthLevelChangedHandler(
                      wealthLevel.id
                    )}
                  />
                </td>
              </tr>
            `
          )}
        </table>
      </section>
    `;
  }

  private partyWealthLevelChangedHandler(
    id: string
  ): (event: InputTextEvent) => void {
    return (event: InputTextEvent) => {
      this.dispatchEvent(
        new CustomEvent<WealthLevelModelUpdate>("party-wealth-level-change", {
          detail: {
            id: id,
            checked: event.target.checked,
          },
          bubbles: true,
          composed: true,
        })
      );
    };
  }

  private individualWealthLevelChangedHandler(
    id: string
  ): (event: InputTextEvent) => void {
    return (event: InputTextEvent) => {
      this.dispatchEvent(
        new CustomEvent<WealthLevelModelUpdate>(
          "individual-wealth-level-change",
          {
            detail: {
              id: id,
              checked: event.target.checked,
            },
            bubbles: true,
            composed: true,
          }
        )
      );
    };
  }
}
