import { css, html, LitElement } from "lit";
import { customElement, property } from "lit/decorators.js";
import { themeCss } from "./theme.js";
import { when } from "lit/directives/when.js";

@customElement("icon-button")
export class IconButton extends LitElement {
  @property({ type: Boolean })
  on: boolean = false;

  static override styles = [
    themeCss,
    css`
      :host {
        cursor: pointer;
        display: inline-block;
        border-radius: 50%;
        border: 1px solid transparent;
        color: var(--paper-brown-800);
        margin: 0;
      }
      ::slotted(*) {
        pointer-events: none;
        user-select: none;
        background: none;
        box-shadow: none;
        border-radius: 50%;
        border: 1px solid transparent;
        color: var(--paper-brown-800);
        padding: 8px;
        margin: 0;
      }
      :host(:hover) {
        background: none;
        border-color: var(--paper-brown-100);
        box-shadow: inset 0 0 8px #e0dcbf;
      }
      :host(:active) {
        background: none;
        box-shadow: inset 0 0 8px #b8b59d;
      }
      :host(:focus) {
        background: none;
        border-color: #888;
        border-radius: 50%;
        box-shadow: inset 0 0 8px #e0dcbf;
      }
    `,
  ];

  override connectedCallback(): void {
    super.connectedCallback();
    this.role = "button";
  }

  protected override render() {
    return html`
      ${when(
        this.on,
        () => html`<slot name="on" @slotchange=${this.preventFocus}></slot>`,
        () => html`<slot @slotchange=${this.preventFocus}></slot>`
      )}
    `;
  }

  private preventFocus(e: Event) {
    const slot: HTMLSlotElement = e.target as HTMLSlotElement;
    for (const element of slot.assignedElements()) {
      element.setAttribute("tabindex", "-1");
    }
  }
}
