import { LitElement, html, css } from "lit";
import { themeCss } from "./theme.js";
import "./wound-level-view.js";
import "./fatigue-level-view.js";
import { customElement, property } from "lit/decorators.js";
import type {
  AddFatigueLevelRowEventDetail,
  AddWoundLevelRowEventDetail,
  CharacterModel,
  RemoveFatigueLevelRowEventDetail,
  RemoveWoundLevelRowEventDetail,
} from "./model.js";

@customElement("status-view")
export class StatusView extends LitElement {
  @property({ type: Object })
  model!: CharacterModel;
  @property({ type: Boolean })
  locked: boolean = true;

  static override styles = [
    themeCss,
    css`
      :host {
        position: relative;
        display: block;
        padding: 4px 8px;
        border-radius: 2px;
        break-inside: avoid;
        -webkit-region-break-inside: avoid;
      }

      h2 {
        font-family: "IM Fell English", serif;
        font-weight: 400;
        font-style: normal;
        font-size: 20px;
        text-align: center;
        margin: 0;
        padding: 0;
      }

      section {
        display: flex;
        padding: 4px;
        background-color: #fff;
        border-radius: 2px;
        border: 1px solid rgba(0, 0, 0, 0.15);
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15),
          inset 0 0 12px var(--paper-yellow-100);
        gap: 4px 12px;
      }

      wound-level-view {
        flex-basis: 0.1px;
      }

      #wound-levels,
      #fatigue-levels {
        flex: 1;
        display: flex;
        flex-direction: column;
      }

      #wound-edit-ui,
      #fatigue-edit-ui {
        display: flex;
        gap: 4px;
      }

      button {
        flex: 1 0 0;
      }
    `,
  ];

  override render() {
    return html`
      <h2>Status</h2>
      <section>
        <div id="wound-levels">
          ${this.model.status.woundLevels.map(
            (woundLevel, index) =>
              html`<wound-level-view
                .model=${woundLevel}
                .showHeader=${index === 0}
              ></wound-level-view>`
          )}
          ${!this.locked ? this.renderWoundEditUi() : html``}
        </div>
        <div id="fatigue-levels">
          ${this.model.status.fatigueLevels.map(
            (fatigueLevel, index) =>
              html`<fatigue-level-view
                .model=${fatigueLevel}
                .showHeader=${index === 0}
              ></fatigue-level-view>`
          )}
          ${!this.locked ? this.renderFatigueEditUi() : html``}
        </div>
      </section>
    `;
  }

  private renderWoundEditUi() {
    return html`
      <div id="wound-edit-ui">
        <button @click=${this.onAddWoundLevel}>Add wound level</button
        ><button
          .disabled=${this.model.status.woundLevels.length <= 6}
          @click=${this.onRemoveWoundLevel}
        >
          Remove wound level
        </button>
      </div>
    `;
  }

  private renderFatigueEditUi() {
    return html`
      <div id="fatigue-edit-ui">
        <button @click=${this.onAddFatigueLevel}>Add fatigue level</button
        ><button @click=${this.onRemoveFatigueLevel}>
          Remove fatigue level
        </button>
      </div>
    `;
  }

  private onAddWoundLevel(event: Event) {
    this.dispatchEvent(
      new CustomEvent<AddWoundLevelRowEventDetail>("add-wound-level", {
        detail: {},
        bubbles: true,
        composed: true,
      })
    );
  }

  private onRemoveWoundLevel(event: Event) {
    this.dispatchEvent(
      new CustomEvent<RemoveWoundLevelRowEventDetail>("remove-wound-level", {
        detail: {},
        bubbles: true,
        composed: true,
      })
    );
  }

  private onAddFatigueLevel(event: Event) {
    this.dispatchEvent(
      new CustomEvent<AddFatigueLevelRowEventDetail>("add-fatigue-level", {
        detail: {},
        bubbles: true,
        composed: true,
      })
    );
  }

  private onRemoveFatigueLevel(event: Event) {
    this.dispatchEvent(
      new CustomEvent<RemoveFatigueLevelRowEventDetail>("remove-fatigue-level", {
        detail: {},
        bubbles: true,
        composed: true,
      })
    );
  }
}
