import { LitElement, html, css } from "lit";
import { themeCss } from "./theme.js";
import "./connection-view.js";
import { customElement, property } from "lit/decorators.js";
import type { AddConnectionRowEventDetail, CharacterModel } from "./model.js";

@customElement("connections-view")
export class ConnectionsView extends LitElement {
  @property({ type: Object })
  model!: CharacterModel;
  @property({ type: Boolean })
  locked: boolean = true;

  static override styles = [
    themeCss,
    css`
      :host {
        position: relative;
        display: block;
        padding: 4px 8px;
        border-radius: 2px;
        break-inside: avoid;
        -webkit-region-break-inside: avoid;
      }

      h2 {
        font-size: 20px;
        text-align: center;
        margin: 0;
        padding: 0;
      }

      section {
        display: flex;
        flex-wrap: wrap;
        padding: 4px;
        background-color: #fff;
        border-radius: 2px;
        border: 1px solid rgba(0, 0, 0, 0.15);
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15),
          inset 0 0 12px var(--paper-yellow-100);
        gap: 4px 12px;
      }

      connection-view {
        flex: 1;
        min-width: calc(33.3% - 12px);
      }

      #add-ui {
        display: flex;
        justify-content: space-around;
        width: 100%;
      }

      #add-ui > button {
        flex: 1;
        max-width: 33.33%;
      }

      @media screen and (max-width: 650px) {
        connection-view {
          min-width: calc(50% - 12px);
        }
        #add-ui > button {
          max-width: 50%;
        }
      }

      @media screen and (max-width: 500px) {
        connection-view {
          min-width: calc(100% - 12px);
        }
        #add-ui > button {
          max-width: 100%;
        }
      }
    `,
  ];

  override render() {
    return html`
      <h2>Connections & Bonds</h2>
      <section>
        ${this.model.connections.map(
          (connection) =>
            html`<connection-view .model=${connection}></connection-view>`
        )}
        ${!this.locked ? this.renderAddUi() : html``}
      </section>
    `;
  }

  private renderAddUi() {
    return html`
      <div id="add-ui">
        <button @click=${this.onAddButtonClicked}>Add row</button>
      </div>
    `;
  }

  private onAddButtonClicked(event: Event) {
    this.dispatchEvent(
      new CustomEvent<AddConnectionRowEventDetail>("add-connection-row", {
        detail: {},
        bubbles: true,
        composed: true,
      })
    );
  }
}
