import { LitElement, html, css } from "lit";
import { themeCss } from "./theme.js";
import { parseIntOrZero, renderIntOrBlank } from "./parsing.js";
import { customElement, property } from "lit/decorators.js";
import type {
  CharacterModelUpdate,
  SpecializationModel,
  SpecializationModelUpdate,
} from "./model.js";
import type { InputTextEvent } from "./types.js";
import { createIcons, Plus } from "lucide";
import "./lucide-icon";

createIcons({ icons: { Plus } });

@customElement("specialization-view")
export class SpecializationView extends LitElement {
  @property({ type: Object })
  model!: SpecializationModel;
  @property({ type: Number })
  generalXp: number = 0;
  @property({ type: Boolean })
  locked: boolean = true;

  static override styles = [
    themeCss,
    css`
      :host {
        display: flex;
        align-items: baseline;
      }

      :host > * {
        margin: 0 2px;
      }

      :host > :first-child {
        margin-left: 0;
      }

      :host > :last-child {
        margin-right: 0;
      }

      #specialization-name {
        flex: 1;
        flex-basis: 0;
        width: 0;
      }

      input[type="number"] {
        width: 24px;
        flex: none;
      }

      #bonus {
        font-size: 13px;
        width: 36px;
        flex: none;
      }
    `,
  ];

  override render() {
    return html`
      <button
        id="buy-button"
        class="buy-button"
        ?disabled=${!this.canAffordRank || !this.model.name}
        title=${this.canAffordRank
          ? `Buy a rank of this specialization for ${this.xpCostToRaise} XP`
          : `Cannot afford a rank of this specialization ` +
            `(need ${this.xpCostToRaise} XP, ` +
            `have ${this.xpAvailable})`}
        @click=${this.onBuyRankButtonClicked}
      >
        <lucide-icon .icon=${Plus} .size=${16}></lucide-icon>
      </button>
      <input
        type="text"
        id="specialization-name"
        .value=${this.model.name}
        @change=${this.onNameFieldChanged}
      />
      <input
        type="number"
        id="rank"
        min="1"
        ?readonly=${this.locked}
        .value=${renderIntOrBlank(this.model.rank)}
        @change=${this.onRankFieldChanged}
      />
      <div id="bonus">${this.model.rank ? `+${this.model.rank}` : ""}</div>
      <input
        type="number"
        id="xp"
        min="0"
        .value=${renderIntOrBlank(this.model.xp)}
        @change=${this.onXpFieldChanged}
      />
    `;
  }

  get xpAvailable() {
    return (this.generalXp ?? 0) + (this.model.xp ?? 0);
  }

  get xpCostToRaise() {
    return (this.model.rank ?? 0) + 1;
  }

  get canAffordRank() {
    return this.xpAvailable >= this.xpCostToRaise;
  }

  onNameFieldChanged(event: InputTextEvent) {
    this.dispatchEvent(
      new CustomEvent<SpecializationModelUpdate>("specialization-change", {
        detail: {
          id: this.model.id,
          change: { name: event.target.value },
        },
        bubbles: true,
        composed: true,
        cancelable: false,
      })
    );
  }

  onRankFieldChanged(event: InputTextEvent) {
    this.dispatchEvent(
      new CustomEvent<SpecializationModelUpdate>("specialization-change", {
        detail: {
          id: this.model.id,
          change: { rank: parseIntOrZero(event.target.value) },
        },
        bubbles: true,
        composed: true,
        cancelable: false,
      })
    );
  }

  onXpFieldChanged(event: InputTextEvent) {
    this.dispatchEvent(
      new CustomEvent<SpecializationModelUpdate>("specialization-change", {
        detail: {
          id: this.model.id,
          change: { xp: parseIntOrZero(event.target.value) },
        },
        bubbles: true,
        composed: true,
        cancelable: false,
      })
    );
  }

  onBuyRankButtonClicked(event: Event) {
    const generalXpSpent = Math.max(
      0,
      this.xpCostToRaise - (this.model.xp ?? 0)
    );
    this.dispatchEvent(
      new CustomEvent<SpecializationModelUpdate>("specialization-change", {
        detail: {
          id: this.model.id,
          change: {
            rank: (this.model.rank ?? 0) + 1,
            xp: Math.max(0, (this.model.xp ?? 0) - this.xpCostToRaise),
          },
        },
        bubbles: true,
        composed: true,
        cancelable: false,
      })
    );
    if (generalXpSpent > 0) {
      this.dispatchEvent(
        new CustomEvent<CharacterModelUpdate>("character-change", {
          detail: {
            change: { xp: this.generalXp - generalXpSpent },
          },
          bubbles: true,
          composed: true,
          cancelable: false,
        })
      );
    }
  }
}
