import { LitElement, html, css } from "lit";
import { themeCss } from "./theme.js";
import { customElement, property } from "lit/decorators.js";
import type { ConnectionModel, ConnectionModelUpdate } from "./model.js";
import type { InputTextEvent } from "./types.js";

@customElement("connection-view")
export class ConnectionView extends LitElement {
  @property({ type: Object })
  model!: ConnectionModel;

  static override styles = [
    themeCss,
    css`
      :host {
        display: flex;
        align-items: baseline;
        justify-content: baseline;
        box-sizing: border-box;
        font-size: 13px;
        gap: 0 4px;
      }

      .name {
        min-width: 32px;
        flex: 1;
      }

      .relationship {
        min-width: 64px;
        flex: 2;
      }
    `,
  ];

  override render() {
    return html`
      <input
        type="text"
        class="name"
        .value=${this.model.name}
        @change=${this.onNameChanged}
      /><input
        type="text"
        class="relationship"
        .value=${this.model.relationship}
        @change=${this.onRelationshipChanged}
      />
    `;
  }

  private onNameChanged(event: InputTextEvent) {
    this.dispatchEvent(
      new CustomEvent<ConnectionModelUpdate>("connection-change", {
        detail: {
          id: this.model.id,
          change: { name: event.target.value },
        },
        bubbles: true,
        composed: true,
        cancelable: false,
      })
    );
  }

  private onRelationshipChanged(event: InputTextEvent) {
    this.dispatchEvent(
      new CustomEvent<ConnectionModelUpdate>("connection-change", {
        detail: {
          id: this.model.id,
          change: { relationship: event.target.value },
        },
        bubbles: true,
        composed: true,
        cancelable: false,
      })
    );
  }
}
