/**
 * Attempts to parse `input` as an integer in base 10.
 * 
 * If parsing fails, rather than returning `NaN` as `parseInt` would, this
 * function instead returns 0.
 */
export function parseIntOrZero(input: string): number {
  const parsed = Number.parseInt(input, 10);
  return isNaN(parsed) ? 0 : parsed ?? 0;
}

/**
 * Attempts to stringify `input` as a base-10 integer.
 * 
 * If `input` is anything other than a well-behaved number (such as `undefined`
 * or `NaN`), returns the empty string.  If `input` is not an integer, it will
 * be rounded to the nearest integer before being rendered as a string.
 */
export function renderIntOrBlank(input: number|undefined): string {
  if (input === undefined || isNaN(input)) {
    return '';
  } else {
    return Math.round(input).toString();
  }
}