import { LitElement, html, css } from "lit";
import { themeCss } from "./theme.js";
import { customElement, property } from "lit/decorators.js";
import type {
  AddEquipmentEventDetail,
  AddItemRowEventDetail,
  CharacterModel,
} from "./model.js";
import "./equipment-view";
import "./item-view";

@customElement("inventory-view")
export class InventoryView extends LitElement {
  @property({ type: Object })
  model!: CharacterModel;
  @property({ type: Boolean })
  locked: boolean = true;

  static override styles = [
    themeCss,
    css`
      :host {
        display: block;
        padding: 4px 8px;
        border-radius: 2px;
        page-break-inside: avoid;
        break-inside: avoid;
        -webkit-region-break-inside: avoid;
      }

      h2 {
        font-size: 20px;
        text-align: center;
        margin: 0;
        padding: 0;
      }

      section {
        display: flex;
        flex-wrap: wrap;
        background-color: #fff;
        padding: 4px;
        border-radius: 2px;
        border: 1px solid rgba(0, 0, 0, 0.15);
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15),
          inset 0 0 12px var(--paper-yellow-100);
        gap: 4px 12px;
      }

      #equipment,
      #items {
        flex: 1;
      }

      button {
        width: 100%;
      }

      @media screen and (max-width: 500px) {
        section {
          flex-direction: column;
        }
      }
    `,
  ];

  override render() {
    return html`
      <h2>Equipment & Items</h2>
      <section>
        <div id="equipment">
          ${this.model.inventory.equipment.map(
            (equipment, index) =>
              html`<equipment-view
                .model=${equipment}
                .showHeader=${index === 0}
                .locked=${this.locked}
              ></equipment-view>`
          )}
          ${!this.locked
            ? html`<button @click=${this.onAddEquipmentButtonClicked}>
                Add row
              </button>`
            : html``}
        </div>
        <div id="items">
          ${this.model.inventory.items.map(
            (item, index) =>
              html`<item-view
                .model=${item}
                .showHeader=${index === 0}
                .locked=${this.locked}
              ></item-view>`
          )}
          ${!this.locked
            ? html`<button @click=${this.onAddItemButtonClicked}>
                Add row
              </button>`
            : html``}
        </div>
      </section>
    `;
  }

  private onAddEquipmentButtonClicked(event: Event) {
    this.dispatchEvent(
      new CustomEvent<AddEquipmentEventDetail>("add-equipment-row", {
        detail: {},
        bubbles: true,
        composed: true,
        cancelable: false,
      })
    );
  }

  private onAddItemButtonClicked(event: Event) {
    this.dispatchEvent(
      new CustomEvent<AddItemRowEventDetail>("add-item-row", {
        detail: {},
        bubbles: true,
        composed: true,
        cancelable: false,
      })
    );
  }
}
