import { LitElement, html, css } from "lit";
import { customElement, property } from "lit/decorators.js";
import { createElement, type IconNode } from "lucide";

@customElement("lucide-icon")
export class LucideIcon extends LitElement {
  @property({ type: Array })
  icon!: IconNode;

  @property({type: Number })
  size: number = 24;

  @property({type: Number })
  strokeWidth: number = 2;

  static override styles = [
    css`
      :host {
        --icon-width: 24px;
        --icon-height: 24px;
        display: block;
        width: var(--icon-width);
        height: var(--icon-height);
      }
      :host > * {
        width: var(--icon-width);
        height: var(--icon-height);
      }
    `,
  ];

  override render() {
    this.style.setProperty('--icon-width', `${this.size}px`);
    this.style.setProperty('--icon-height', `${this.size}px`);
    const element = createElement(this.icon);
    element.setAttribute('stroke-width', this.strokeWidth.toString());
    return html`${element}`;
  }
}
